import no1 from './no1.vue'
import no2 from './no2.vue'
import no3 from './no3.vue'
import no4 from './no4.vue'

var index = {
	install(Vue){
        Vue.component('no1',no1)
        Vue.component('no2',no2)
        Vue.component('no3',no3)
        Vue.component('no4',no4)
		
	
	}
}

export default index


